.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-header {
  background-color: rgb(1, 27, 19);
  min-height: 5vh;
  display: flex;
  /* flex-direction: column; */
  align-items: left;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
}

.mainSection {
  background-color: white;
  min-height: 80vh;
  
  font-size: calc(10px + 2vmin);
  color: black;
}

.questionSection {
  background-color: rgb(217, 226, 217);
  min-height: 80vh;
  width: 70%;
  float: left;
  /* display: flex; */
  position: relative;
  align-items: left;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
}

.questionText {
  padding: 10px;
  position: relative;
  min-height: 70%;
  justify-content: center;
  width: 90%;
  color: black;
  font-size: 15px;
}

.questionText p {
  text-align: left;
  justify-content: center;
}

.questionOptions {
  position: relative;
  height: 25%;
  justify-content: left;
  width: 100%;
  color: black;
  margin:0;
}

.questionButtons {
  margin:0;
  height: 5%;
  padding: 0px;
}

.btn-next {
  margin: 0 10% 0 10%;
  border: 2px;
  cursor: pointer;
  width: 100px;
  background-color: rgb(17, 156, 248);
  height: 50px;
}

.btn-prev {
  margin: 0 10% 0 10%;
  border: 2px;
  cursor: pointer;
  width: 100px;
  background-color: rgb(17, 156, 248);
  height: 50px;
}

.p-left {
  margin:0;
  position: relative;
  width: 30%;
  align-items: left;
  justify-content: left;
}

.p-center{
  margin:0;
  position: relative;
  width: 30%;
  align-items: left;
  justify-content: left;
}

.p-right{
  margin:0;
  position: relative;
  width: 30%;
}


.btn-review {
  margin: 0 10% 0 10%;
  border: 2px;
  min-width: 100px;
  cursor: pointer;
  background-color: rgb(17, 156, 248);
  height: 50px;
}

.questionSwitcherSection {
  background-color: rgb(219, 217, 217);
  min-height: 80vh;
  /* display: flex; */
  align-items: center;
  justify-content: center;
  width: 30%;
  float: right;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto; 
  font-size: calc(10px + 2vmin);
  color: white;
}

.start-btn {
  margin:200px;
  height: 80px;
  background-color:rgb(17, 156, 248);
  border: 2px;
  font-size: 40px;
  width: 400px;
  border-radius: 2% ;
  cursor: pointer;
}
.questionOptions-ul {
  list-style-type: none;
  justify-content: left;
  text-align: left;
  color: black;
  font-size: 16px;
}
.questionOptions-ul label {
  font-size: 16px;
}
.questionSwitcherSection-button {
  width: 50px;
  height: 50px;
  float: left;
  margin: 2%;
  cursor: pointer;
}

.questionSwitcherSection-button-unanswered {
  background-color: rgb(242, 240, 238);
  color: black;
}
.questionSwitcherSection-button-review {
  background-color: rgb(40, 8, 248);
  color: black;
}
.questionSwitcherSection-button-answered {
  background-color: rgb(20, 221, 53);
  color: black;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
